@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff2");
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.visually-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}

.theme-light {
  background-color: #ffffff;
}

.theme-dark {
  background-color: #81b3d2;
  color: #ffffff;
}

.body {
  margin: 0 auto;
  max-width: 1280px;
  background: #f2f2f2;
  font-family: "PT Sans", arial, helvetica, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.header {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  background-color: #ffffff;
  min-height: 57px;
}

.header__toggle {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 17px;
  cursor: pointer;
}

.nav {
  display: inline-block;
  text-transform: uppercase;
  width: 100%;
  height: 100%;
}

.nav__list {
  display: flex;
  justify-content: space-between;
}

/* Меню открытия  */
@media (max-width: 767px) {
  .nav-menu__opened {
    z-index: 10;
    margin: 0;
  }
  .nav-menu__opened .site-list__link {
    color: #ffffff;
  }
  .nav-menu__opened .site-list__link:hover {
    color: #ffffff;
  }
  .nav-menu__opened .site-list__link:active {
    color: #87b5d1;
  }
  .nav-menu__opened .site-list__link:focus {
    color: #ffffff;
  }
  .nav-menu__opened .site-list__item:hover {
    background-color: #669ec0;
    border-top: 1px soild #669ec0;
    border-bottom: 1px solid #669ec0;
  }
  .nav-menu__opened .site-list__item:active {
    background-color: #5496bd;
    border-top: 1px soild #5496bd;
    border-bottom: 1px solid #5496bd;
  }
  .nav-menu__opened .site-list__item:disabled {
    background-color: #e5e5e5;
    border-top: 1px soild #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
  }
  .nav-menu__opened .site-list__item {
    border-top: 1px solid #678fa8;
    border-bottom: 1px solid #9ac2db;
    padding-top: 19px;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-menu__opened .nav__list {
    position: absolute;
    padding: 0;
    margin: -29px 0;
    flex-direction: column;
    width: 100%;
  }
  .nav-menu__opened .nav-menu__opened .nav__button {
    z-index: 10;
    top: 0;
    right: auto;
    left: 0;
  }
  .nav-menu__opened .nav-menu__opened .nav__button::before, .nav-menu__opened .nav-menu__opened .nav__button::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
  }
  .nav-menu__opened .nav-menu__opened .nav__button::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .nav-menu__opened .nav-menu__opened .nav__button::after {
    transform: rotate(-45deg);
  }
  .nav-menu__opened .nav-menu__opened .nav__button:active::before, .nav-menu__opened .nav-menu__opened .nav__button:active::after {
    background-color: fade(#ffffff, 30%);
  }
}

/* Меню закрытия */
.nav-menu__closed .site-list {
  display: none;
}
.nav-menu__closed .site-list__link {
  color: currentColor;
}
.nav-menu__closed .site-list__link--active {
  color: #766357;
}
@media screen and (min-width: 768px) {
  .nav-menu__closed .site-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}

/* Бургерное меню и кнопка закрытия меню */
.nav__button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 67px;
  height: 51px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}
@media (min-width: 1200px) {
  .nav__button {
    display: none;
  }
}

.nav__button::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 24px;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18.5px 0 0 #81b3d2;
}

.nav__button:hover::before {
  background-color: #b3d1f2;
  box-shadow: 0 9px 0 0 #b3d1f2, 0 18.5px 0 0 #b3d1f2;
}

.nav__button:active::before {
  background-color: #dae9f2;
  box-shadow: 0 9px 0 0 #dae9f2, 0 18.5px 0 0 #dae9f2;
}

.nav-menu__opened .nav__button {
  z-index: 10;
  top: 0;
  right: 0;
}
.nav-menu__opened .nav__button::before, .nav-menu__opened .nav__button::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 5px;
  background-color: #ffffff;
}
.nav-menu__opened .nav__button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav-menu__opened .nav__button::after {
  transform: rotate(-45deg);
}
.nav-menu__opened .nav__button:hover::before, .nav-menu__opened .nav__button:hover::after {
  background-color: rgba(242, 243, 244, 0.9333333333);
}
.nav-menu__opened .nav__button:active::before, .nav-menu__opened .nav__button:active::after {
  background-color: rgba(254, 254, 254, 0.4392156863);
}

/* Меню без JS */
@media (max-width: 767px) {
