.text-list--index {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .text-list--index {
    flex-direction: row;
  }
}

.attraction__check {
  display: flex;
  margin-top: 20px;
  flex-direction: column;
  gap: 16px;
  align-items: flex-start;
}
@media (min-width: 768px) {
  .attraction__check {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 9px;
    padding-left: 84px;
    padding-right: 68px;
  }
}
@media (min-width: 1200px) {
  .attraction__check {
    justify-content: space-around;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 26px;
    padding-top: 41px;
    padding-left: 44px;
    padding-right: 1px;
  }
}

.attraction__title {
  margin-top: 0;
  margin-left: -2px;
  padding: 0;
  text-align: center;
}

.attraction__checkbox {
  appearance: none;
  position: absolute;
}

.attraction__text {
  margin: 0;
  padding: 0;
  padding-left: 16px;
}

.attraction__fake-input {
  width: 46px;
  height: 46px;
  border-radius: 8px;
  background: #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.attraction__fake-input:hover {
  background-color: #e5e5e5;
}

.attraction__fake-input:active {
  background-color: #d5d5d5;
  content: url("/img/tick-active.svg");
  padding-top: 8px;
  padding-right: 12px;
  padding-bottom: 12px;
  padding-left: 12px;
}

.attraction__checkbox:checked ~ .attraction__fake-input::before {
  content: url("/img/tick.svg");
  width: 40px;
  height: 40px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rating__title {
  margin: 0;
  padding: 0;
  text-align: center;
}

.rating__radio-box {
  margin-top: 19px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: flex-start;
}
@media (min-width: 768px) {
  .rating__radio-box {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 9px;
    margin-left: 36px;
    margin-right: 36px;
  }
}
@media (min-width: 1200px) {
  .rating__radio-box {
    display: flex;
    justify-content: center;
    flex-direction: row;
    gap: 65px;
    margin-top: 35px;
  }
}

.rating__text {
  margin: 0;
  padding: 0;
  padding-left: 16px;
  padding-top: 2px;
}

.rating__radio {
  appearance: none;
  position: absolute;
}

.rating__fake-input {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background: #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rating__fake-input:hover {
  background-color: #e5e5e5;
}

.rating__fake-input:active {
  background-color: #d5d5d5;
}

.rating__radio:checked ~ .rating__fake-input::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #333333;
}

.rating__radio-label:active .rating__fake-input::before {
  background: #a5a5a5;
}

.text-area {
  font-family: "PT Sans", arial, helvetica, sans-serif;
  font-weight: 700;
  font-size: 18px;
  width: 100%;
  background: #f2f2f2;
